<template>
<div
  ref="app"
  @drop.prevent.stop
  @drag.prevent.stop
  @dragover.prevent.stop
  @dragenter.prevent.stop
  class="app">

    <div class="app__left">

      <div class="app__inputs">

        <div class="app__top">
          <equalizer></equalizer>
          <helper></helper>
          <move-line
            v-model="time"
            @click="timeClick"
            @mousemove="showTime"
            @mouseleave="hideHelper"
            :height="20"
            :topRound="false"
            :maxValue="100"></move-line>
        </div>

        <div class="app__bottom">

          <div class="app__container app__container_v">
            <move-line
              @mousemove="showVolume"
              @mouseleave="hideHelper"
              v-model="volume"
              :height="10"
              :maxValue="100"></move-line>
            <move-line
              @mousemove="showSpeed"
              @mouseleave="hideHelper"
              v-model="speed"
              :height="10"
              :maxValue="200"></move-line>
          </div>

          <div class="app__container app__container_c">
            <flatButton
              :type="'prev'"
              @click="prevClick">
            </flatButton>

            <flatButton
              v-show="showPlay"
              :type="'play'"
              @click="playClick">
            </flatButton>
            <flatButton
              v-show="!showPlay"
              :type="'pause'"
              @click="pauseClick">
            </flatButton>

            <flatButton
              :type="'next'"
              @click="nextClick">
            </flatButton>
          </div>

        <div class="app__container app__container_r">
            <flatButton
              ref="listButton"
              @mousemove="showHelperList"
              @mouseleave="hideHelper"
              :type="'list'"
              @click="listClick">
            </flatButton>
            <flatButton
              ref="randomButton"
              @mousemove="showHelperRandom"
              @mouseleave="hideHelper"
              :type="'random'"
              @click="randomClick">
            </flatButton>
            <flatButton
              ref="loopButton"
              @mousemove="showHelperLoop"
              @mouseleave="hideHelper"
              :type="'loop'"
              @click="loopClick">
            </flatButton>
          </div>

        </div>

      </div>

      <div class="app__frameworks">
        <div class="app__framework-vue"></div>
        <div class="app__framework-electron"></div>
      </div>

    </div>

    <div class="app__right">
      <track-list
        @selectplay="trackSelectPlay"
        @clearplay="clearPlayed"></track-list>
    </div>

</div>
</template>


<script>
import equalizer from "./components/Equalizer";
import helper from "./components/Helper";
import moveLine from "./components/MoveLine";
import flatButton from "./components/FlatButton";
import trackList from "./components/TrackList";
import { eventEmitter } from "./main";

export default {
  components: {
    equalizer,
    helper,
    moveLine,
    flatButton,
    trackList
  },

  data() {
    return {
      changeTimeFlag: false,
      audio: new Audio(),
      havePlay: false,
      hideDelay: 500,
      showPlay: true,
      trackTimeMax: 0,
      trackTimeCurrent: 0,
      time: 0,
      volume: 100,
      speed: 100,
      helperListText: "  ",
      helperRandomText: "  ",
      helperLoopText: "  "
    };
  },

  methods: {

    //     mm:ss
    formatTime(seconds) {
      seconds = Math.floor(seconds);
      let mm = String(Math.floor(seconds / 60));
      let ss = String(Math.floor(seconds % 60));
      if (mm.length == 1) {
        mm = "0" + mm;
      }
      if (ss.length == 1) {
        ss = "0" + ss;
      }
      return `${mm}:${ss}`;
    },

    showTime() {
      if (this.havePlay) {
        let cur = this.formatTime(this.trackTimeCurrent);
        let max = this.formatTime(this.trackTimeMax);
        this.showHelper(`${cur} / ${max}`);
      } else {
        this.showHelper(" ");
      }
    },

    showVolume() {
      this.showHelper(`: ${this.volume}%`);
    },

    showSpeed() {
      this.showHelper(`: ${this.speed}%`);
    },

    clearPlayed() {
      this.havePlay = false;
      this.pauseClick();
    },

    showHelper(text) {
      eventEmitter.$emit("show-helper", text);
    },

    showHelperList() {
      this.showHelper(this.helperListText);
    },

    showHelperRandom() {
      this.showHelper(this.helperRandomText);
    },

    showHelperLoop() {
      this.showHelper(this.helperLoopText);
    },

    hideHelper(flagDuration) {
      if (flagDuration) {
        window.setTimeout((self) => {
          self.hideHelper();
        }, this.hideDelay, this);
      } else {
        eventEmitter.$emit("hide-helper");
      }
    },

    trackSelectPlay(track) {
      let self = this;
      let fr = new FileReader();
      fr.onload = file => {
        self.audio.src = file.target.result;
        self.audio.playbackRate = (this.speed / 100).toFixed(2);
        self.audio.volume = (this.volume / 100).toFixed(2);
      };
      fr.readAsDataURL(track.track.file);
    },

    nextClick() {
      if (!this.havePlay) return;
      eventEmitter.$emit("track-list-next");
    },

    prevClick() {
      if (!this.havePlay) return;
      eventEmitter.$emit("track-list-prev");
    },

    playClick() {
      if (!this.havePlay) return;
      eventEmitter.$emit("play-equalizer");
      this.audio.play();
      this.showPlay = false;
    },

    pauseClick() {
      eventEmitter.$emit("pause-equalizer");
      this.audio.pause();
      this.showPlay = true;
    },

    loopClick() {
      if (this.loopClick.f) {
        this.helperLoopText = "  ";
      } else {
        this.helperLoopText = "  ";
      }
      this.showHelperLoop();
      this.loopClick.f = !this.loopClick.f;
      eventEmitter.$emit("track-list-toggleLoopCurrent");
    },

    randomClick() {
      if (this.randomClick.f) {
        this.helperRandomText = "  ";
      } else {
        this.helperRandomText = "  ";
      }
      this.showHelperRandom();
      this.randomClick.f = !this.randomClick.f;
      eventEmitter.$emit("track-list-toggleRandomNext");
    },

    listClick() {
      if (this.listClick.f) {
        this.helperListText = "  ";
      } else {
        this.helperListText = "  ";
      }
      this.$refs.app.classList.toggle("app_compact");
      this.showHelperList();
      this.listClick.f = !this.listClick.f;
      eventEmitter.$emit("track-list-toggleVisibility");
    },

    timeClick() {
      if (!this.havePlay) return;
      this.audio.currentTime = (this.trackTimeMax / 100 * this.time).toFixed(2);
    },
  },

  watch: {

    volume() {
      this.audio.volume = (this.volume / 100).toFixed(2);
    },

    speed() {
      this.audio.playbackRate = (this.speed / 100).toFixed(2);
    },

  },

  mounted() {
    let self = this;
    let delta = 5;
    //   
    window.addEventListener("keydown", e => {
      let key = e.key.toLowerCase();
      switch (key) {
        case "delete":
          eventEmitter.$emit("track-list-deleteSelected");
          break;
        case "arrowup":
          self.prevClick();
          break;
        case "arrowdown":
          self.nextClick();
          break;
        case "arrowright":
          self.nextClick();
          break;
        case "arrowleft":
          self.prevClick();
          break;
        case " ":
        case "p":
          if (self.showPlay) {
            self.playClick();
          } else {
            self.pauseClick();
          }
          break;
        case "enter":
          self.$refs.listButton.click({
            target: self.$refs.listButton.$el
          });
          if (self.listClick.f) {
            self.showHelper("  ");
          } else {
            self.showHelper("  ");
          }
          self.hideHelper(true);
          break;
        case "l":
          self.$refs.loopButton.click({
            target: self.$refs.loopButton.$el
          });
          if (self.loopClick.f) {
            self.showHelper("  ");
          } else {
            self.showHelper("  ");
          }
          self.hideHelper(true);
          break;
        case "r":
          self.$refs.randomButton.click({
            target: self.$refs.randomButton.$el
          });
          if (self.randomClick.f) {
            self.showHelper("  ");
          } else {
            self.showHelper("  ");
          }
          self.hideHelper(true);
          break;
        case "+":
          if (e.altKey) {
            if (self.speed <= 200 - delta) {
              self.speed += delta;
            } else {
              self.speed = 200;
            }
            self.showSpeed();
            self.hideHelper(true);
          } else {
            if (self.volume <= 100 - delta) {
              self.volume += delta;
            } else {
              self.volume = 100;
            }
            self.showVolume();
            self.hideHelper(true);
          }
          break;
        case "-":
          if (e.altKey) {
            if (self.speed >= 50 + delta) {
              self.speed -= delta;
            } else {
              self.speed = 50;
            }
            self.showSpeed();
            self.hideHelper(true);
          } else {
            if (self.volume >= delta) {
              self.volume -= delta;
            } else {
              self.volume = 0;
            }
            self.showVolume();
            self.hideHelper(true);
          }
          break;
      }
    });

    window.addEventListener("resize", e => {
      eventEmitter.$emit("move-line-resize");
    });
  },

  created() {
    let self = this;

    this.audio.addEventListener("loadedmetadata", (e) => {
      self.trackTimeCurrent = 0;
      self.trackTimeMax = e.target.duration;
    });

    this.audio.ontimeupdate = (e) => {
      self.trackTimeCurrent = e.target.currentTime;
      self.time = Math.trunc(100 / self.trackTimeMax * self.trackTimeCurrent);
    }

    this.audio.addEventListener("canplay", () => {
      self.havePlay = true;
      self.playClick();
    });

    this.audio.addEventListener("ended", () => {
      self.trackTimeCurrent = self.trackTimeMax;
      self.nextClick();
    });
  },

};
</script>


<style scoped lang="scss">
@import "base.scss";

$left-padding-h: 20px;
$left-padding-v: 15px;

$margin-right: 10px;

.app {
  display: flex;
  justify-content: space-between;
  height: 100%;
  overflow: hidden;
  user-select: none;
  cursor: pointer;

  &_compact {
    .app__left {
      min-height: 340px;
      width: 100%;
    }
    .app__right {
      display: none;
    }
    .app__inputs {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
    }
    .app__top {
      height: 100%;
    }
    .equalizer {
      height: calc(100% - 40px) !important;
    }
    .helper {
      bottom: 10% !important;
      right: 2% !important;
    }
    .app__frameworks {
      display: none;
    }
  }

  &__frameworks {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    height: 100%;
  }

  &__framework-vue,
  &__framework-electron {
    opacity: 0.3;
    transition-duration: $animation-duration;
    transition-timing-function: $animation-timing-function;
    transition-property: opacity;

    &:hover {
      opacity: 1;
    }
  }

  &__framework-vue {
    width: 100px;
    height: 100px;
    background-image: url("./img/vue.png");
    background-repeat: no-repeat;
    background-size: cover;
  }

  &__framework-electron {
    margin-bottom: 10px;
    width: 200px;
    height: 30px;
    background-image: url("./img/electron.svg");
    background-repeat: no-repeat;
    background-size: contain;
  }

  &__left {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 40%;
    min-width: 400px;
    background-color: darken($color-main-back, 2);
  }

  &__right {
    width: 60%;
  }

  &__inputs {
    padding: $left-padding-v $left-padding-h;
    background-color: $color-main-back;
  }

  &__top {
    height: 370px;
    position: relative;
    margin-bottom: $left-padding-v;

    .equalizer {
      height: 350px;
    }

    .helper {
      position: absolute;
      bottom: 15px + $margin-right;
      right: $margin-right;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .move-line {
      width: 100px;
    }
  }

  &__container {
    display: flex;
    align-items: center;
    width: 100%;

    .flat-button {
      margin-right: $margin-right;

      &:last-child {
        margin-right: 0;
      }
    }

    &_v {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: flex-start;
      padding-right: $margin-right;
      height: 40px;
    }

    &_c {
      justify-content: center;

      .flat-button {
        margin-right: $margin-right / 2;

        &:last-child {
          margin-right: $margin-right;
        }
      }
    }

    &_r {
      justify-content: flex-end;
    }
  }
}
</style>



